export default {
	data() {
		return {
			list:[
				
			],
			active:false,
			value:'',
			activeIndex:0,
			text:'全部',
			textShow:false
		}
	},
	onLoad() {

	},
	computed:{
		listData(){
			let list=JSON.parse(JSON.stringify(this.list));
			console.log(list);
			let newList=[]
			//点击全部
			if(this.activeIndex===0){
				this.text='全部'
				return list
			}
			// 点击待办事项
			if(this.activeIndex===1){
				list.forEach((item)=>{
					if(!item.checked){
						newList.push(item)
					}
				})
				this.text='待办'
				return newList
			}
			//点击已完成
			if(this.activeIndex===2){
				list.forEach((item)=>{
					if(item.checked){
						newList.push(item)
					}
				})
				this.text='已完成'
				return newList
			}
		}
	},
	methods: {
		//打开输入框
		fun(){
			if(this.active){
				this.close()
			}else{
				this.open()
			}
		},
		// 打开动画
		open(){
			this.active=true;
			this.$nextTick(()=>{
				setTimeout(()=>{
					this.textShow=true
				},50)
			})
		},
		// 关闭动画
		close(){
			this.textShow=false
			this.$nextTick(()=>{
				setTimeout(()=>{
					this.active=false
				},500);
			})
		},
		fun1(){
			console.log(this.value);
			if(this.value===''){
				uni.showToast({
					title:'请输入内容',
					icon:'none'
				})
				return
			}
			this.list.unshift({
				id:'id'+new Date().getTime(),
				content:this.value,
				checked:false
			})
			this.value=''
			this.close()
		},
		// 点击列表触发
		finish(id){
			let index=this.list.findIndex((item)=>item.id===id)
			console.log(id);
			this.list[index].checked=!this.list[index].checked;
		},
		fun3(index){
			this.activeIndex=index;
		}
	}
}
